.credit__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.credit__main-text {
    margin-bottom: 30px;
}

.credit__main-text p {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 1.6;
}

.credit__img {
    display: block;
    margin: 0 auto 30px;
}

.credit__list {
    padding: 0;
    margin: 0;
    margin-bottom: 37px;
    list-style: none;
}

.credit__item {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 104px 20px 20px 20px;
    margin-bottom: 31px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        height: 20px;
        width: 64px;
        height: 64px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.credit__item--first {
    &::before {
        background-image: url("../img/icon-credit1.svg");
    }    
}

.credit__item--second {
    &::before {
        background-image: url("../img/icon-credit2.svg");
    }    
}

.credit__item-title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";

    span {
        color: #D32A34;
    }
}

.credit__text p {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 15px;
}

@media (min-width: 576px) {
    .credit__title {
        font-size: 36px;
    }

    .credit__main-text {
        margin-bottom: 40px;
    }

    .credit__main-text p {
        margin-bottom: 28px;
        font-size: 16px;
    }

    .credit__img {
        width: 450px;
        height: auto;
        margin-bottom: 40px;
    }

    .credit__list {
        margin-bottom: 57px;
    }

    .credit__item {
        padding: 30px 30px 30px 124px;
        margin-bottom: 30px;

        &::before {
            left: 30px;
            top: 30px;
        }
    }
}    

@media (min-width: $tablet-width) {
    .credit__title {
        text-align: left;
        margin-bottom: 40px;
    }

    .credit__item-title {
        font-size: 20px;
    }   

    .credit__list {
        margin-bottom: 65px;
    }
}    

@media (min-width: 992px) {
    .credit {
        display: grid;
        grid-template-columns: 1fr 366px;
        gap: 40px;
    }

    .credit__title {
        font-size: 40px;
    }

    .credit__main-text {
        margin-bottom: 0;
    }

    .credit__main-text p {
        font-size: 18px;
        margin-bottom: 30px;
    }

    .credit__img {
        width: 366px;
    }

    .credit__list {
        grid-column: 1/3;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 40px;
        margin-bottom: 60px;
    }

    .credit__item {
        padding: 20px 20px 20px 114px;

        &::before {
            top: 20px;
            left: 23px;
        }
    }
}    

@media (min-width: $desktop-width) {
    .credit {
        position: relative;
        grid-template-columns: 1fr 524px;
    }

    .credit__title {
        margin-bottom: 62px;
    }

    .credit__img {
        position: absolute;
        top: 125px;
        right: 22px;
        width: 524px;
    }

    .credit__list {
        grid-column: 1/2;
        gap: 35px;
        margin-bottom: 82px;
    }    

    .credit__item {
        display: block;
        padding: 134px 40px 40px 40px;

        &::before {
            top: 40px;
            left: 40px;
        }
    }
}    
