.page-furniture {
    .notice {
        padding-top: 0px;
    }

    .advantages__list {
        margin-bottom: 46px;
    }

    .answers {
        padding-top: 40px;
    }
}

@media (min-width: 576px) {
    .page-furniture .notice {
        padding-top: 5px;
        padding-bottom: 60px;
    }

    .page-furniture .notice__text {
        font-size: 14px;
    }

    .page-furniture .cards {
        padding-bottom: 33px;
    }

    .page-furniture .advantages__list {
        margin-bottom: 55px;
    }

    .page-furniture .answers {
        padding-top: 50px;
    }
}    

@media (min-width: $tablet-width) {
    .page-furniture .notice {
        padding-bottom: 64px;
    }

    .page-furniture .notice__text-wrap {        
        right: 30px;
        left: 30px;  
        top: 50px;      
    }

    .page-furniture .notice__text-wrap-add {        
        padding: 20px;
        background-color: rgba(255,255,255,.6);
        border-radius: 12px;
        margin-bottom: 30px;
    }

    .page-furniture .notice__text {
        margin-bottom: 0;
    }

    .page-furniture .notice__title {
        margin-bottom: 12px;
    }

    .page-furniture .notice__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 27px;
    }

    .page-furniture .notice__item {
        margin-right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }

    .page-furniture .cards {
        padding-bottom: 0;
    }

    .page-furniture .advantages__list {
        margin-bottom: 67px;
    }

    .page-furniture .answers {
        padding-top: 21px;
    }

    .page-furniture .form-price {
        padding-top: 25px;
    }
}    

@media (min-width: 992px) {
    .page-furniture .notice__text-wrap {
        top: 30px;
        right: 224px;
        left: 42px;
        padding-top: 26px;
    }

    .page-furniture .notice__text-wrap-add {
        max-width: 75%;
        background: none;
        margin-bottom: 20px;
    }

    .page-furniture .notice__title {
        margin-bottom: 15px;
    }

    .page-furniture .notice__text {
        font-size: 15px;
    }

    .page-furniture .notice__list {
        padding-left: 20px;
        padding-right: 0;
        display: flex;
        gap: 0;
    }    

    .page-furniture .notice__item {
        margin-right: 22px;

        &:last-child {
            margin-right: 0;
        }
    }

    .page-furniture .notice {
        padding-bottom: 91px;
    }

    .page-furniture .form-price {
        padding-top: 14px;
    }

    .page-furniture .advantages__list {
        margin-bottom: 89px;
    }

    .page-furniture .answers {
        padding-top: 68px;
    }
}    

@media (min-width: $desktop-width) {
    .page-furniture .notice {
        padding-bottom: 100px;
    }

    .page-furniture .breadcrump {
        padding-bottom: 40px;
    }

    .page-furniture .notice__text-wrap {
        width: 770px;
    }

    .page-furniture .notice__text-wrap-add {
        max-width: 100%;
        padding-top: 60px;
        padding-left: 28px;
        padding-right: 40px;
    }    

    .page-furniture .notice__item {
        padding-right: 20px;
    }

    .page-furniture .notice__list {
        max-width: 86%;
    }

    .page-furniture .form-price {
        padding-top: 90px;
    }

    .page-furniture .advantages__list {
        margin-bottom: 115px;
    }

    .page-furniture .answers {
        padding-top: 10px;
    }
}    