.page-card {
    .form-price {
        padding-bottom: 25px;
    }

    .answers {
        padding-top: 38px;
    }    
}

@media (min-width: 576px) {
    .page-card .form-price {
        padding-bottom: 36px;
    }
}    

@media (min-width: $tablet-width) {
    .page-card .form-price {
        padding-bottom: 48px;
    }

    .page-card .answers {
        padding-top: 18px;
    }
}    

@media (min-width: 992px) {
    .page-card .form-price {
        padding-top: 45px;
        padding-bottom: 64px;
    }

    .page-card .answers {
        padding-top: 65px;
    }
}  

@media (min-width: $desktop-width) {
    .page-card .form-price {
        padding-top: 129px;
        padding-bottom: 80px;
    }

    .page-card .form-price__lead {
        display: block;
    }

    .page-card .form-price__title-desktop {
        display: block;
    }

    .page-card .form-price__title {
        display: none;
    }

    .page-card .answers {
        padding-top: 10px;
    }
}    