.contacts {
    
}

.contacts__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.contacts__item {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 29px 20px 29px 64px;
    margin-bottom: 20px;
    font-size: 14px;

    span {
        display: block;
        margin-bottom: 10px;        
    }

    span:first-child {
        color: #C79B67;
    }

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 20px;
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-size: contain;
        transform: translateY(-50%);
    }
}

.contacts__item--email {

    &::before {
        background-image: url("../img/icon-email-bottom.svg");
    }
    
}

.contacts__item--phone {
    padding: 19px 20px 19px 64px;

    &::before {
        background-image: url("../img/icon-call.svg");
    }
    
}

.contacts__item--time {
    padding: 19px 20px 19px 64px;

    &::before {
        background-image: url("../img/icon-clock.svg");
    }

    span:nth-child(2) {
        margin-bottom: 0;
    }
    
}

.contacts__item--map,  .contacts__item--map2 {
    padding: 20px 20px 19px 64px;    

    span {
        margin-bottom: 4px;
    }

    p {
        margin: 0;
        padding: 0;
        padding-top: 10px;
    }

    &::before {
        top: 20px;
        transform: none;
    }
    
}

.contacts__item--map {
    
    &::before {
        background-image: url("../img/icon-map.svg");
    }
}    

.contacts__item--map2 {
    
    &::before {
        background-image: url("../img/icon-attention-brown.svg");
    }
}   

p.contacts__text {
    margin-bottom: 20px;
}


.contacts__link {
    color: #585858;
    display: block;
    font-size: 14px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.contacts__link-map {
    color: #969696;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.contacts__socials {
    padding: 33px;
    border: 1px solid #efefef;    
    border-radius: 16px;
    margin-bottom: 20px;
}

.contacts__wrap--address {
    margin-bottom: 40px;
}

@media (min-width: 576px) {
    .contacts__title {
        font-size: 36px;
        margin-bottom: 20px;
    }

  .contacts__wrap--data {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-bottom: 30px;

    .contacts__item {
        margin-bottom: 0;
      }
  }  

  .contacts__item {
    margin-bottom: 30px;
  }
  

  .contacts__socials {
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 0;

    .socials__item {
        margin-right: 10px;
    }

    .socials__item:last-child {
        margin-right: 0;
    }
  }    

    p.contacts__text { 
        margin-bottom: 39px;
    }    

    .contacts__wrap--address {
        margin-bottom: 50px;
    }
  
}    

@media (min-width: $tablet-width) {
    .contacts__title {
        text-align: left;
        margin-bottom: 40px;
    }

    .contacts__socials {
    
        .socials__item {
            margin-right: 20px;
        }
    }  
    
    .contacts__wrap--address {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }    

    .contacts__item {
        margin-bottom: 0;
    }

    p.contacts__text {
        margin-bottom: 20px;
    }

    .contacts__wrap--address {
        margin-bottom: 60px;
    }
}  

@media (min-width: 992px) {
    .contacts__title {
        font-size: 40px;
        margin-bottom: 60px;
    }

    .contacts__item {
        padding: 40px 30px 40px 74px;
        font-size: 15px;

        span {
            font-size: 14px;
        }
    }

    .contacts__item--phone {
        padding: 30px 30px 30px 74px;
    }

    .contacts__item--time {
        padding: 30px 30px 30px 74px;
    }

    .contacts__item--map {
        padding: 30px 30px 30px 74px;
    }

    .contacts__item--map2 {
        padding: 30px 30px 30px 74px;

        &::before {
            top: 27px;
            left: 28px;
        }
    }

    .contacts__link {
        font-size: 15px;
    }

    .contacts__link-map {
        font-size: 14px;
    }
}    

@media (min-width: $desktop-width) {
    .contacts {
        display: grid;
        grid-template-columns: 1fr 920px;
    }

    .contacts__title {
        grid-column: 1/3;
        margin-bottom: 78px;
    }

    .contacts__wrap--data {
        grid-column: 1/3;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 40px;
        margin-bottom: 59px;
    }

    .contacts__item {
        padding: 50px 40px 50px 84px;

        &::before {
            left: 40px;
        }
    }

    .contacts__item--phone {
        padding: 40px 40px 40px 84px;
    }

    .contacts__item--time {
        padding: 40px 40px 40px 84px;
    }

    .contacts__wrap--address {
        display: block;
        border: 1px solid #efefef;
        border-radius: 30px;
        padding: 40px 40px 0 40px;

        .contacts__item {
            border: none;
            margin-bottom: 40px;
        }
    }

    .contacts__item--map {
        padding: 0 0 0 44px;

        &::before {
            top: -3px;
            left: -2px;
        }
    }

    .contacts__item--map2 {
        padding: 0 0 0 44px;

        &::before {
            top: -3px;
            left: -2px;
        }
    }    
}    