.pagination {
    margin-bottom: 37px;
}


.pagination__more {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding-left: 20px;
    margin: 0 auto;
    font-size: 14px;
    font-family: "TTFirsNeue-medium";
    height: 60px;
    color: #000000;
    text-decoration: none;
    position: relative;
    text-align: center;
    transition: all .2s ease;
    width: 100%;
    box-sizing: border-box;
    max-width: 340px;
    border: 1px solid #efefef;
    border-radius: 12px;

    span {
        padding-right: 25px;
    }

   svg {
        margin-right: 10px;
        margin-bottom: 3px;        
   }

    &:hover {        
        color: #BB1C26;
        border: 1px solid #D5D6DA;
        transition: all .2s ease;
    }

    &:active {
        color: #BB1C26;
        border: 1px solid #D5D6DA;
        transition: all .2s ease;
    }
}

@media (min-width: 576px) {
    .pagination {
        margin-bottom: 52px;
    }

    .pagination__more {
        max-width: 521px;
    }
}    

@media (min-width: $tablet-width) {
    .pagination {
        margin-bottom: 70px;
    }

    .pagination__wrap {
        max-width: 696px;
        display: grid;
        grid-template-columns: 1fr 258px;
        column-gap: 10px;
    }

    .pagination__list {
        margin-bottom: 0;       
    }

    .pagination__more {
        margin: 0;
        width: 258px;
    }

}    

@media (min-width: 992px) {
    .pagination {
        margin-bottom: 42px;
    }

    .pagination__wrap {
        max-width: 930px;
        grid-template-columns: 1fr 210px;
    }

    .pagination__more {
        width: 210px;
    }
}    

@media (min-width: $desktop-width) {
    .pagination {
        margin-bottom: 65px;
    }

    
    .pagination__wrap {
        max-width: 970px;
        gap: 15px;
    }
}    