.page-promo {
    .cards {
        display: none;
    }
}

@media (min-width: 576px) {
    .page-promo {
        .cards {
            display: block;
            padding-bottom: 23px;
        }
    }
}   

@media (min-width: $tablet-width) {
    .page-promo {
        .cards {
            padding-bottom: 0;
        }
    }

    .page-promo .answers {
        padding-top: 12px;
    }
}  

@media (min-width: 992px) {
    .page-promo .answers {
        padding-top: 74px;
    }
}

@media (min-width: $desktop-width) {
    .page-promo .answers {
        padding-top: 94px;
    }
}    




