.page-reviews {
    .reviews {
        padding-top: 0;
        padding-left: 0;
        background: none;
    }

    .reviews__slide {
        width: 100%;
        height: 360px;
        margin-bottom: 30px;

        video {
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    .reviews__text {
        width: 100%;
        left: 0;
    }

    .reviews__slide--desktop {
        display: none;
    }

    .reviews .video-controls {
        top: 50%;
        left: 9%;
    }

    .answers {
        padding-top: 0px;
    }
}

@media (min-width: 576px) {
    .page-reviews .reviews__slide {
        width: 100%;
        height: 320px;
        padding-top: 0;
    }

    .page-reviews .reviews .video-controls {
        top: 43%;
        left: 12%;
    }
}    

@media (min-width: $tablet-width) {
    .page-reviews .reviews__slide {
        width: 100%;
        height: 380px;
    }

    .page-reviews .reviews .video-controls {
        top: 53%;
        left: 9%;
    }

    .page-reviews .pagination {
        margin-bottom: 66px;
    }

    .page-reviews .cards {
        padding-bottom: 15px;
    }
}  

@media (min-width: 992px) {
    .page-reviews .reviews__slide--desktop {
        display: block;
        padding-left: 0;
        padding-right: 0;
    }

    .page-reviews .reviews {
        padding-right: 0;
    }

    .page-reviews .pagination {
        margin-bottom: 90px;
    }

    .page-reviews .answers {
        padding-top: 60px;
    }
}    

@media (min-width: $desktop-width) {
    .page-reviews .reviews {
        padding-bottom: 35px;

        &::before {
            height: 0;
            width: 0;
        }
    }


    .page-reviews .reviews__slide {
        margin-top: 0;
        height: 320px;
        margin-bottom: 40px;
    }

    .page-reviews .reviews .video-controls {
        top: 40%;
        left: 9%;
    }

    .page-reviews .reviews .comments__big-video.reviews__slide .video-controls {
        top: 45%;
        left: 43%;
    }

    .page-reviews .pagination {
        margin-bottom: 100px;
    }

    .page-reviews .answers {
        padding-top: 79px;
    }

}    