.comment {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 30px 30px 10px 30px;

    .comments__link {
        position: relative;
        z-index: 2;
    }
}

.comment__link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: pointer;
    background-color: transparent;
    z-index: 1;
}

.comment__info {
    display: grid;
    grid-template-columns: 50px 1fr;
    gap: 20px;
    align-items: center;
    margin-bottom: 13px;
}

.comment__info-wrap {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 10px;

    &::before {
        content: "";
        position: absolute;
        top: 45%;
        left: -27px;
        width: 20px;
        height: 20px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-ya.svg");
    }
}

.comment__name {
    padding: 0;
    margin: 0;
    margin-bottom: 2px;
    width: 100%;
    font-size: 15px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.comment__date {
    color: #969696;
    font-size: 14px;
    width: calc(100% - 90px);
    padding-left: 10px;
}

.comment__img {
    border-radius: 16px;
    width: 90px;
}

.comment__text {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 15px;
    max-height: 70px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical; 
}


@media (min-width: 576px) { 
    .comment .comments__link {
        width: 265px;
    }

}    

@media (min-width: $tablet-width) {
    .comment__text {
        max-height: 120px;
        -webkit-line-clamp: 5;
        margin-bottom: 22px;
    }
}    

@media (min-width: $desktop-width) {
    .comment {
        padding: 40px 40px 20px 40px;
    }

    .comment__text {     
        max-height: 100px;
        -webkit-line-clamp: 4;
    }
}    