.projects__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.projects__cards {
    margin-bottom: 40px;
}

.projects__card {
    margin-bottom: 30px;
}

.projects__card .card__slider {
    width: 100%;
}

.projects__cards .counts__count {
    display: block;
    position: absolute;
    top: 30px;
    left: 30px;
    padding: 8px 12px;
    color: #fff;
    background-color: #000;
    border-radius: 6px;
    font-size: 13px;
    line-height: 13px;
    font-family: "TTFirsNeue-medium";
    letter-spacing: .02em;
}

.projects__cards .counts__count-slides {
    color: #7f7f7f;
}

.projects__cards .card__title {
    font-size: 16px;
    margin-bottom: 19px;
}

.projects__wrap {
    display: none;
}

.projects__wrap--mobile {
    display: block;
}

.projects__cards .slick-dotted.slick-slider {
    margin-bottom: 0;
}

.projects__cards .slider .slick-dots {
    bottom: 18px;
    text-align: left;
}

@media (min-width: 576px) {
    .projects__cards {
        margin-bottom: 49px;
    }

    .projects__title { 
        font-size: 36px;
    }    

    .projects__cards {       
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .projects__card {
        margin-bottom: 0;
    }

    .projects__wrap {
        min-width: 0;
    }

    .projects__wrap--tablet {
        display: block;
    }
}    

@media (min-width: $tablet-width) {
    .projects__title {
        text-align: left;
        padding-left: 39px;
    }

    .projects__cards {
        margin-bottom: 55px;
    }

    .projects__cards .card__title {
        font-size: 18px;
        margin-bottom: 12px;
    }

}    

@media (min-width: 992px) {
    .projects__title {
        font-size: 40px;
        padding-left: 30px;
        margin-bottom: 27px;
    }

    .projects__wrap--big-tablet {
        display: block;
    }

    .projects__cards {       
        display: flex;
        justify-content: space-between;    
        flex-wrap: wrap;       
        gap: 0;    
        margin-bottom: 35px; 
    }

    .projects__wrap {
        width: calc((100% - 60px)/3);
        margin-bottom: 30px;
    }

}  

@media (min-width: $desktop-width) {
    .projects__cards {
        margin-bottom: 65px;
    }

    .projects__title {
        padding-left: 25px;
        padding-right: 25px;
        margin: 0 auto 50px;
        max-width: 1390px;
    }

    .projects__wrap {
        display: block;
        width: calc((100% - 80px)/3);
        margin-bottom: 40px;
    }

        
    .projects__wrap:nth-child(5n + 4),
    .projects__wrap:nth-child(5n + 5) {
        width: calc((100% - 40px)/2);
        height: 260px;        

        .projects__card {
            display: grid;
            grid-template-columns: 260px 1fr;
            height: 260px;
            align-items: flex-start;
        }

        .card__image {
            height: 260px;
            border-radius: 18px 0 0 18px;
        }

        .card__link {
            position: static;
            //right: auto;
            //left: 40px;
        }

        .card__info {
            height: 260px;
            padding: 40px;
        }

        .card__title {
            margin-bottom: 22px;
            font-size: 16px;
        }

        .card__price {
            margin-bottom: 33px;
        }

        .card .slider .slick-dots {
            bottom: 43px;
        
        }

        .counts__count {
            top: 33px;
            left: 30px;
        }
    }

    .projects__cards .card__title {
        margin-bottom: 40px;
    }

    .projects__cards .counts__count {
        top: 28px;
        left: 40px;
    }  

    .projects__cards .card__info {
        padding: 30px 40px 43px;
    }
}    