.page-credit .cards {
    padding-bottom: 18px;
}

.page-credit .materials {
    display: none;
}

@media (min-width: 576px) {    
    .page-credit .cards {
        padding-bottom: 24px;
    }
}    

@media (min-width: $tablet-width) {
    .page-credit .cards {
        padding-bottom: 13px;
    }
    
    .page-credit .answers {
        padding-top: 0;
    }
}    


@media (min-width: 992px) {    
    .page-credit .materials {
        display: block;
        padding-top: 50px;
    }

    .page-credit .answers {
        padding-top: 60px;
    }
}    

@media (min-width: $desktop-width) {
    .page-credit .cards {
        padding-top: 30px;
    }

    .page-credit .answers {
        padding-top: 80px;
    }
}
