.payment__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.payment__list {
    margin: 0;
    margin-bottom: 38px;
    padding: 0;
    list-style: none;
}

.payment__item {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 102px 20px 20px 20px;
    margin-bottom: 28px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        height: 20px;
        width: 64px;
        height: 64px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.payment__item--cart {
    &::before {
        background-image: url("../img/icon-payment1.svg");
    }    
}

.payment__item--non-cash {
    &::before {
        background-image: url("../img/icon-payment2.svg");
    }    
}

.payment__item--cash {
    &::before {
        background-image: url("../img/icon-payment3.svg");
    }    
}

.payment__item--account {
    &::before {
        background-image: url("../img/icon-payment4.svg");
    }    
}


.payment__item-title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.payment__text {
    padding: 0;
    margin: 0;
    font-size: 16px;
}

@media (min-width: 576px) {
    .payment__title {
        font-size: 36px;
        margin-bottom: 36px;
    }

    .payment__list {
        margin-bottom: 57px;
    }

    .payment__item {
        padding: 29px 30px 29px 124px;
        margin-bottom: 29px;

        &::before {
            top: 50%;
            left: 30px;
            transform: translateY(-50%);
        }
    }
} 

@media (min-width: $tablet-width) {
    .payment__title {
        text-align: left;
        margin-bottom: 48px;
    }

    .payment__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        margin-bottom: 64px;
    }

    .payment__item {
        display: block;
        padding: 124px 30px 28px 30px;
        margin-bottom: 0;

        &::before {
            top: 30px;
            transform: none;
        }
    }

    .payment__item-title {
        font-size: 20px;
    }
}  

@media (min-width: 992px) {
    .payment__title {
        font-size: 40px;
        margin-bottom: 54px;
    }
}    

@media (min-width: $desktop-width) {
    .payment__title {
        margin-bottom: 80px;
    }

    .payment__list {
        grid-template-columns: repeat(4, 1fr);
        gap: 40px;
        margin-bottom: 135px;
    }

    .payment__item {
        padding: 134px 40px 38px 40px;

        &::before {
            top: 40px;
            left: 40px;
        }
    }

}    