.promo__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    color: #000000;
    font-size: 30px;
    font-family: "TTFirsNeue-Bold";
    text-align: center;
}

.promo__item {
    position: relative;
    padding: 104px 20px 20px 20px;
    border: 1px solid #efefef;
    margin-bottom: 19px;
    border-radius: 16px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        height: 20px;
        width: 64px;
        height: 64px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}


.promo__item--date {
    &::before {
        background-image: url("../img/icon-promo1.svg");
    }   
}

.promo__item--bonus {
    &::before {
        background-image: url("../img/icon-promo2.svg");
    }   
}

.promo__item--wedding {
    &::before {
        background-image: url("../img/icon-promo3.svg");
    }   
}

.promo__item--birthday {
    &::before {
        background-image: url("../img/icon-promo4.svg");
    }   
}

.promo__item--elderly {
    &::before {
        background-image: url("../img/icon-promo5.svg");
    }   
}

.promo__item--sales {
    &::before {
        background-image: url("../img/icon-promo6.svg");
    }   
}

.promo__item-title {
    padding: 0;
    margin: 0;
    margin-bottom: 9px;
    color: #000000;
    font-size: 18px;
    font-family: "TTFirsNeue-demibold";

    span {
        color: #D32A34;
    }    
}

.promo__text {
    padding: 0;
    margin: 0;
    font-size: 15px;
}

.promo__img {
    display: none;
}

.promo__img-mobile {
    display: block;
    margin-bottom: 0;
}

@media (min-width: 576px) {
    .promo {
        margin-bottom: 61px;
    }

    .promo__title {
        font-size: 36px;
        margin-bottom: 27px;
    }

    .promo__item {
        padding: 29px 30px 29px 124px;
        margin-bottom: 29px;
        
        &::before {
            top: 53px;
            left: 30px;
        }
    }

    .promo__text {
        font-size: 16px;
    }
    
    .promo__img-mobile {
        width: 390px;
        height: auto;
        margin: 0 auto;
    }

}    

@media (min-width: $tablet-width) {
    .promo {
        margin-bottom: 70px;
    }

    .promo__title {
        text-align: left;
        margin-bottom: 40px;
    }

    .promo__item  {
        &::before {
            top: 40px;
        }
    }

    .promo__item-title { 
        font-size: 20px;
    }    

    .promo__img-mobile {
        width: 450px;
    }
}    

@media (min-width: 992px) {
    .promo {
        margin-bottom: 62px;
    }

    .promo__title {
        font-size: 40px;
        margin-bottom: 47px;
    }

    .promo__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .promo__img-mobile {
        display: none;
    }

    .promo__img {
        display: block;
        width: 450px;
        height: auto;
    }

    .promo__wrap {
        grid-column: 1/3;
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
        align-items: flex-end;
        

        .promo__item:nth-child(6) {
            order: 3;
        }      
        
        .promo__item:nth-child(3) {
            order: 2;          
        } 

        .promo__item:nth-child(4) {
            order: 1;            
        } 

        .promo__item:nth-child(5) {
            order: 3;            
        } 
       
    }
}    

@media (min-width: $desktop-width) {
    .promo {
        position: relative;
    }

    .promo__title {
        margin-bottom: 20px;
    }

    .promo__list {
        padding-top: 60px;
        align-items: flex-end;
        column-gap: 40px;
    }

    .promo__item {
        padding: 39px 40px 39px 134px;
        margin-bottom: 39px;

        &::before {
            top: 50px;
            left: 40px;
        }
    }

   
    .promo__img {
        position: absolute;
        top: 35px;
        right: 63px;
        width: 561px;
    }

    .promo__wrap {
        column-gap: 40px;
    }
}    