.purchase {
    margin-bottom: 38px;
}

.purchase__title {
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    font-family: "TTFirsNeue-Bold";
    font-size: 20px;
    color: #000000;
    line-height: 1.4;
    letter-spacing: 0.02em;

    span {
        color: #D32A34;
    }
    
}

.purchase__first-word {
    display: block;
}

.purchase__slider {
    margin-bottom: 30px;
}

.purchase__slide {
    position: relative;
    width: 100%;
    height: auto;
    overflow: hidden;
    padding-top: 24%;
    border-radius: 30px;
}

.purchase__img-wrap {
    position: relative;
    width: 100%;
    height: 100%;
}


.purchase__img {
    border-radius: 30px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.purchase .counts__count {
    display: block;
    position: absolute;
    top: 40px;
    left: 40px;
    padding: 8px 12px;
    color: #ffffff;
    background-color: #000000;
    border-radius: 6px;
    font-size: 13px;
    line-height: 13px;
    font-family: "TTFirsNeue-medium";
    letter-spacing: 0.02em;
}

.purchase .counts__count-slides {
    color: #7F7F7F;
}

.purchase__text {
    margin-bottom: 20px;
}

.purchase__list {
    padding: 0;
    margin: 0;    
    list-style: none;
}

.purchase__item {
    position: relative;
    margin-bottom: 19px;
    padding: 15px 20px 15px 64px;
    border: 1px solid #efefef;
    border-radius: 16px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 30px;
        height: 15px;
        width: 15px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-check-brown.svg");
    }
}


.purchase .slider .slick-dots {
    bottom: 30px;
    text-align: left;
    padding-left: 40px;
}

@media (min-width: 576px) {
    .purchase__title {
        font-size: 24px;        
        margin-bottom: 22px;
    }

    .purchase__first-word {
        display: inline;
    }

    .purchase__item {
        display: inline-block;
    }

    .purchase__text {
        margin-bottom: 30px;
    }

    .purchase__item {
        padding: 15px 14px 15px 64px;
    }    
}   

@media (min-width: $tablet-width) {
    .purchase {
        margin-bottom: 48px;
    }

    .purchase__slider.slick-dotted.slick-slider {
        margin-bottom: 44px;
    }

    .purchase__text {
        margin-bottom: 37px;
    }
}    

@media (min-width: 992px) {
    .purchase {
        display: grid;
        grid-template-columns: 370px 1fr;
        column-gap: 50px;
        margin-bottom: 40px;
    }

    .purchase__title {
        padding-top: 8px;
        font-size: 28px;
    }

    .purchase__text p {
        font-size: 18px;
    }

    .purchase__slider.slick-dotted.slick-slider {
        margin-bottom: 0;
    }

    .purchase__slide {
        position: relative;
        width: 370px;
        height: 490px;
        padding-top: 0;
    }

    .purchase__item:last-child {
        margin-bottom: 0;
    }

    .purchase__wrap {
        display: flex;
        flex-direction: column;
    }

    .purchase__list {
        margin-top: auto;
        max-width: 700px;
    }

    .purchase .slider .slick-dots {
        left: 0;
        bottom: -33px;
        padding-left: 0;
        text-align: center;
    }
}    

@media (min-width: $desktop-width) {
    .purchase {
        grid-template-columns: 440px 1fr;
        column-gap: 175px;
        align-items: flex-end;
        margin-bottom: 92px;
    }

    .purchase__slide {
        width: 440px;
        height: 560px;
    }

    .purchase__title {
        font-size: 32px;
    }

    .purchase__wrap {
        display: block;
        padding-bottom: 30px;
    }

    .purchase .slider .slick-next {
        top: 41%;
        right: -110px;
    }

    .purchase .slider .slick-prev {
        top: 41%;
        left: -104px;
    }
}    