.delivery__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.delivery__list {
    margin: 0;
    margin-bottom: 28px;
    padding: 0;
    list-style: none;
}

.delivery__item {
    position: relative;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 19px 20px 19px 104px;
    margin-bottom: 28px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 20px;
        height: 20px;
        width: 64px;
        height: 64px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.delivery__item--city {
    &::before {
        background-image: url("../img/icon-delivery1.svg");
    }    
}

.delivery__item--region {
    &::before {
        background-image: url("../img/icon-delivery2.svg");
    }    
}

.delivery__item--time {
    &::before {
        background-image: url("../img/icon-delivery3.svg");
    }    
}

.delivery__item--free {
    &::before {
        background-image: url("../img/icon-delivery4.svg");
    }    
}


.delivery__item-title {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    font-size: 20px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.delivery__text {
    padding: 0;
    margin: 0;
    font-size: 16px;
}

.delivery__note {
    position: relative;
    padding: 90px 21px 20px 20px;
    margin-bottom: 25px;
    border-radius: 30px;
    border: 10px solid #EFEFEF;

    p {
        padding: 0;
    margin: 0;
    font-size: 15px;
    }    

    &::before {
        content: "";
        position: absolute;
        top: 25px;
        left: 20px;
        height: 20px;
        width: 44px;
        height: 52px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-check-red.svg");
    }
}

.delivery__note-title {
    padding: 0;
    margin: 0;
    margin-bottom: 8px;
    color: #000000;
    font-size: 18px;
    font-family: "TTFirsNeue-demibold";
}

@media (min-width: 576px) {
    .delivery__title {
        font-size: 36px;
        text-align: left;
        margin-bottom: 35px;
    }
    .delivery__list {
        margin-bottom: 40px;
    }

    .delivery__item {
        padding: 29px 30px 29px 124px;
        margin-bottom: 30px;

        &::before {
            left: 30px;           
        }
    }

    .delivery__note {
        padding: 30px 30px 30px 94px;
        margin-bottom: 23px;

        &::before {
            top: 50%;
            left: 30px;
            transform: translateY(-50%);
        }
    }
} 

@media (min-width: $tablet-width) {
    .delivery__title {
        margin-bottom: 60px;
    }

    .delivery__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        margin-bottom: 55px;
    }

    .delivery__item {
        padding: 122px 30px 30px 30px;
        margin-bottom: 0;

        &::before {
            top: 30px;
            transform: none;
        }
    }

    .delivery__note {
        border: 15px solid #efefef;
        margin-bottom: 74px;
    }

    .delivery__note-title {
        font-size: 20px;
    }

    .delivery__note p {
        font-size: 16px;
    }
}   

@media (min-width: 992px) {
    .delivery__title {
        font-size: 40px;
        margin-bottom: 60px;
    }

    .delivery__note {
        margin-bottom: 35px;
    }
}    

@media (min-width: $desktop-width) {
    .delivery__title {
        margin-bottom: 80px;
    }

    .delivery__list {
        grid-template-columns: repeat(4, 1fr);
        gap: 40px;
        margin-bottom: 80px;
    }

    .delivery__item {
        padding: 133px 40px 39px 40px;

        &::before {
            top: 40px;
            left: 40px;
        }
    }

    .delivery__note {
        padding: 38px 40px 38px 106px;
        margin-bottom: 62px;

        &::before {
            left: 40px;
        }
    }

}   