.partners {
    margin-bottom: 20px;
}

.partners__title {
    padding: 0;
    margin: 0;
    margin-bottom: 40px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
}

.partners__list {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
}

.partners__item {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 31px 19px;
}

.partners__note-title {
    padding: 0;
    margin: 0;
    margin-bottom: 8px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";

    span {
        color: #D32A34;
    }
}

.partners__note {
    position: relative;
    padding: 90px 20px 20px 20px;
    margin-bottom: 30px;
    border-radius: 30px;
    border: 10px solid #EFEFEF;

    p {
        padding: 0;
    margin: 0;
    font-size: 15px;
    }    

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        height: 20px;
        width: 44px;
        height: 52px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-check-red.svg");
    }
}

.partners__list-add {
    padding: 0;
    margin: 0;
    margin-bottom: 38px;
    list-style: none;
}

.partners__item-add {
    padding: 19px 20px 19px 32px;
    border: 1px solid #efefef;
    margin-bottom: 20px;
    border-radius: 16px;
    display: grid;
    grid-template-columns: 43px 1fr;
    column-gap: 20px;
    align-items: center;
}

.partners__count {
    font-family: "Viga";
    font-size: 30px;
    color: #B4864F;
}

.partners__text {
    padding: 0;
    margin: 0;
    font-size: 14px;
}

@media (min-width: 576px) {
    .partners__title {
        font-size: 36px;
        margin-bottom: 48px;
    }

    .partners__img--first {
        width: 183px;
        height: auto;
    }

    .partners__img--second {
        width: 182px;
        height: auto;
    }

    .partners__item {
        padding: 39px 19px;
    }

    .partners__note {
        padding: 30px 30px 30px 94px;

        &::before {
            top: 30px;
            left: 30px;
        }
    }

    .partners__item-add {
        padding: 19px 20px;
    }   

    .partners__count {
        font-size: 44px;
        margin-bottom: 8px;
    }

    .partners__list-add {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
    }

    .partners__item-add {
        display: block;
        text-align: center;
    }

    .purchase__slide {
        padding-top: 23.5%;
    }   
}    

@media (min-width: $tablet-width) {
    .partners__title {
        text-align: left;
        font-size: 40px;
        margin-bottom: 58px;
    }

    .partners__item {
        padding: 74px 57px;
    }

    .partners__img--first {
        width: 215px;
    }

    .partners__img--second {
        width: 215px;
    }

    .partners__list {
        margin-bottom: 25px;
    }

    .partners__note {
        padding: 30px 35px 30px 94px;
        border: 15px solid #efefef;
        margin-bottom: 38px;
    }

    .partners__list-add {
        margin-bottom: 50px;
    }
}    

@media (min-width: 992px) {
    .partners__title {
        margin-bottom: 63px;
    }

    .partners__note-title {
        font-size: 20px;
    }

    .partners__note {
        margin-bottom: 34px;
    }

    .partners__note p {
        font-size: 16px;
    }   

    .partners__text {
        font-size: 16px;
    }

    .partners__item-add {
        display: flex;
        padding: 28px 30px;
        text-align: left;
    }

    .partners__count {
        margin-bottom: 0;
    }

    .partners__list-add {
        margin-bottom: 70px;
    }
}    

@media (min-width: $desktop-width) {
    .partners {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 40px;
    }

    .partners__list {
        column-gap: 40px;
        margin-bottom: 35px;
    }

    .partners__item {
        padding: 90px 45px;
    }

    .partners__img--first {
        width: 215px;
    }

    .partners__img--second {
        width: 215px;
    }

    .partners__title {
        grid-column: 1/3;
        margin-bottom: 90px;
    }

    .partners__note {
        padding: 39px 36px 30px 107px;
        margin-bottom: 35px;

        &::before {
            top: 40px;
            left: 40px;
        }
    }

    .partners__list-add {
        grid-column: 1/3;
        column-gap: 40px;
        margin-bottom: 115px;
    }

    .partners__item-add {
        padding: 40px;
    }
}    