.options {
    overflow-x: auto;
    margin-bottom: 40px;
    padding-left: 20px;
}

/*  scrollbar  Chrome, Safari  Opera */
.options::-webkit-scrollbar {
    display: none;
  }
   
  /*  scrollbar  IE, Edge  Firefox */
.options {
    -ms-overflow-style: none;  /* IE  Edge */
    scrollbar-width: none;  /* Firefox */
}

.options__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-start;
}

.options__item {
    width: auto;
    flex-basis: auto;
    flex-shrink: 0;
    margin-right: 10px;
    border: 1px solid #efefef;
    font-size: 14px;
    color: #000000;
    padding: 12px 20px;
    border-radius: 12px;
    cursor: pointer;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        color: #fff;
        background-color: #b4864f;
    }
}

@media (min-width: 576px) {
    .options {
      padding-left: 33px;
      margin-bottom: 47px;
    }
  }  

@media (min-width: $tablet-width) {
    .options {
        padding-left: 39px;
        overflow: hidden;
    }
}    

@media (min-width: 992px) {
    .options {
        padding-left: 30px;
        margin-bottom: 40px;
    }

    .options__item {
      padding: 17px 30px;
      margin-right: 20px;
    }
  }  

@media (min-width: $desktop-width) {
    .options {
        max-width: 1370px;
        padding-left: 15px;
        padding-right: 15px;
        margin: 0 auto 60px;
    }
}    