.page-not-found {
    .cards .cards__link {
        display: none;
    }   

    .cards {
        padding-bottom: 0;
    }
}

@media (min-width: 576px) {
    .page-not-found .cards {
        padding-bottom: 13px;
    }
}    

@media (min-width: $tablet-width) {
    .page-not-found .cards {
        padding-bottom: 23px;
    }
}    

@media (min-width: 992px) {
    .page-not-found .cards {
        padding-bottom: 81px;
    }
}    

@media (min-width: $desktop-width) {
    .page-not-found .cards {
        padding-top: 28px;
        padding-bottom: 118px;
    }
}    