.comments {
    margin-bottom: 8px;
}

.comments__title {
    padding: 0;
    margin: 0;
    margin-bottom: 24px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}


.comments__list {   
    margin-bottom: 30px;
}


.comments__link {
    display: block;
    padding-top: 12px;
    padding-bottom: 12px;
    margin-bottom: 20px;
    border: 1px solid #efefef;
    border-radius: 12px;
    text-align: center;
    color: #000000;
    font-size: 15px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }

    span:first-child {
        position: relative;
        padding-left: 30px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 2px;
            width: 20px;
            height: 20px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
        }
    }
}

.comments__link--yandex {
    span:first-child {
        &::before {
            background-image: url("../img/icon-ya.svg");
        }
    }    
}

.comments__link--youtube {
    span:first-child {
        &::before {
            background-image: url("../img/icon-yout.svg");
        }
    }    
}

.comments__star {
    position: relative;
    margin-left: 30px;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: -22px;
        width: 18px;
        height: 17px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-star.svg");
    }
}

.comments__item {
    margin-bottom: 33px;
}

.comments__item--desktop {
    display: none;
}

.comments__h {
    display: none;
}

@media (min-width: 576px) {  
    
    .comments {
        margin-bottom: 15px;
    }

    .comments__title {
        font-size: 36px;
        margin-bottom: 28px;
    }

    .comments__list  {
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 20px;
    }


    .comments__video-wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .comments__link span:first-child {
        padding-left: 25px;

        &::before {
            left: -3px;
        }
    }
}  

@media (min-width: $tablet-width) {

    .comments {
        margin-bottom: 25px;
    }

    .comments__title {
        text-align: left;
    }
    
    .comments__text-wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .comments__list {
        max-width: 505px;
        margin-bottom: 20px;
    }
}    

@media (min-width: 992px) {
    .comments__title {
        font-size: 40px;
    }

    .comments__list {
        margin-bottom: 18px;
    }

    .comments__item {
        margin-bottom: 29px;
    }

    .comments__link {
        padding-top: 16px;
        padding-bottom: 16px;
    }

    .comments__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
    }

    .comments__a {
        grid-column: 1/3;
    }

    .comments__wrap3 {
        display: grid;        
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 30px;
        grid-template-areas: "e e j"
        "i i i";
    }
    
    
    .comments__c {
        grid-column: 1/4;
    }

    .comments__e {
        grid-area: e;
    }

    .comments__f {
        display: none;
        grid-area: f;
    }

    .comments__j {
        grid-area: j;
    }

    .comments__h {
        display: none;
        grid-area: h;
    }

    .comments__i {
        grid-area: i;
    }
    
}    

@media (min-width: $desktop-width) {
    .comments .container {
        position: relative;
    }

    .comments__list {
        position: absolute;
        top: 4px;
        right: 25px;
        width: 505px;
    }

    .comments__title {
        margin-bottom: 80px;
    }

    .comments__item {
        margin-bottom: 40px;
    }

    .comments__wrap {
        display: grid; 
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: auto;
        grid-template-areas: "a a b"
        "c c b";
        column-gap: 40px;
        row-gap: 0;
    }

    .comments__a {
        grid-area: a;
    }

    .comments__b {
        grid-area: b;
    }

    .comments__c {
        grid-area: c;
    }

    .comments__wrap3 {
        display: block;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 40px;
    }

    .comments__video-wrap {
        grid-column: 1/3;
    }

    .comments__wrap3 {
        display: grid; 
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: auto;
        grid-template-areas: "e e f"
        "j h h"
        "j i i";
        column-gap: 40px;
        row-gap: 0;
    }
    
    .comments__h {
        display: block;
    }

    .comments__f {
        display: block;
    }

    .comments__video-wrap {
        grid-template-columns: 1fr 1fr;
        column-gap: 40px;
    }    

    .comments__text-wrap {
        column-gap: 40px;
    }

    .comments__item--desktop {
        display: block;
    }

    .comments__big-video.reviews__slide {
        height: 680px;
    }

}    