.page-company {
    .company {
        padding-top: 0;
    }

    .company__title {
        text-align: center;
    }


    .company__text-wrap {
        padding-bottom: 42px;
    }

    .company__person-text {
        padding-right: 0;
    }

    .company__team {
        margin-bottom: 55px;
    }
}

@media (min-width: 576px) {
    .page-company .company__title {
        margin-bottom: 20px;
    }

    .page-company .company__text {
        padding-right: 10px;
        margin-bottom: 28px;
    }

    .page-company .company__team {
        margin-bottom: 63px;
    }    

    .page-company .company__title--partners {
        text-align: left;
        margin-bottom: 50px;
    }    
}    

@media (min-width: $tablet-width) {
    .page-company .company {
        padding-bottom: 27px;
    }

    .page-company .company__title {
        text-align: left;
        margin-bottom: 32px;
    }

    .page-company .company__text {
        margin-bottom: 17px;
    }   

    .page-company .company__team {
        margin-bottom: 70px;
    }

    .page-company .company__title--partners {
        margin-bottom: 60px;
    }    
}    

@media (min-width: 992px) {
    .page-company .company__text {
        margin-bottom: 23px;
    }

    .page-company .company__person.company__person--main {
        margin-bottom: 50px;
    }

    .page-company .company__person {
        margin-bottom: 30px;
    }

    .page-company .company__team {
        margin-bottom: 63px;
    }

    .page-company .company__title--partners {
        margin-bottom: 70px;
    }    
}   

@media (min-width: $desktop-width) {
    .page-company .company__title {
        margin-bottom: 60px;
    }

    .page-company .company__title--partners {
        margin-bottom: 90px;
    }

    .page-company .company__text {
        font-size: 18px;
        max-width: 875px;
        line-height: 1.6;
        margin-bottom: 45px;
    }

    .page-company .company__team {
        margin-bottom: 70px;
    }

    .page-company .company__person-add {
        background-image: url("../img/icon-blockquote-5.svg");
        top: -215px;
        padding-right: 0;
        padding-left: 94px;

        &::before {
            top: 50px;
            left: 40px;
        }
    }


    .page-company .company__person-add span {
        font-size: 16px;
        max-width: none;
        padding-left: 0;
        padding-top: 33px;
        padding-bottom: 39px;
        padding-right: 37px;
        background-color: transparent;
    }   

    .page-company .company {
        padding-bottom: 72px;
    }
}    