ul.page-numbers {
    padding: 0;
    margin: 0 auto 10px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    max-width: 340px;

    li {        
        display: none;
        
        &:first-child {
            display: block;
        }
       
        &:nth-child(2) {
            display: block;
        }

        &:nth-child(3) {
            display: block;
        }

        &:nth-child(4) {
            display: block;
        }

        &:last-child {
            display: block;
        }
    }    
}

a.page-numbers {
    display: flex;
    align-items: center;
    justify-content: center;  
    border-radius: 12px;
    border: 1px solid #efefef;
    width: 55px;
    height: 55px;        
    cursor: pointer;
    color: #585858;

    &:hover {
        color: #000000;
        border: 1px solid #D5D6DA;
    }

    &:active {
        color: #000000;
        border: 1px solid #D5D6DA;
    }
}

a.page-numbers.current {
    background-color: #C79B67;
    color: #ffffff;
}

a.page-numbers.prev {
    position: relative;
    font-size: 0;

    svg {
        width: 7px;
        height: 12px;
    }

    &:hover {       

        svg {
            stroke: #B4864F;
            transition: all .2s ease;
        }
    }

    &:active {       

        svg {            
            stroke: #B4864F;
            transition: all .2s ease;
        }
    }
}

a.page-numbers.next {
    position: relative;
    font-size: 0;
    transition: all .2s ease;

    &:hover {                

        svg {
            stroke: #B4864F;
            transition: all .2s ease;
        }

    }

    &:active {                

        svg {
            stroke: #B4864F;
            transition: all .2s ease;
        }

    }

    svg {
        width: 7px;
        height: 12px;
    }
}

@media (min-width: 576px) {
    ul.page-numbers {
        max-width: 510px;
        flex-wrap: nowrap;
    
        li {

            &:nth-child(5) {
                display: block;
            }
                       
            &:nth-child(6) {
                display: block;
            }
    
           
            /*&:nth-last-child(-n+2) {
                display: block;
            }

            &:nth-last-child(-n+3) {
                display: block;
            }*/
    
        }    

        /*li.dots {
            display: flex;
