            height: 56px;
            padding-bottom: 10px;
            color:#585858;
            box-sizing: border-box;
            border: 1px solid #efefef;
            border-radius: 12px;
        }*/
    }

    a.page-numbers {
        width: 62px;
        height: 62px;
    }    
}   

@media (min-width: $tablet-width) {
    ul.page-numbers {
        max-width: none;
        margin: 0;   

        li {
            &:nth-child(5) a.page-numbers {
                background-color: #F6F3F1;
            }
        }   

        li {
            &:nth-child(6) {
                display: none;
            }
        }    
    }
    
}    

@media (min-width: 992px) {
    ul.page-numbers {
    
        li {       

            &:nth-child(6) {
                display: block;
            }
            
            &:nth-last-child(-n+2) {
                display: block;
            }

            &:nth-last-child(-n+3) {
                display: block;
            }
    
        }        
        
        li.dots {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 62px;
            height: 62px;
            padding-bottom: 10px;
            color:#585858;
            box-sizing: border-box;
            border: 1px solid #efefef;
            border-radius: 12px;
        }
    }

    
}    

