.company {
    padding-top: 36px;
    padding-bottom: 15px;
}

.company__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 30px;
    color: #000000;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
    line-height: 1.4;
}

.company__text-wrap {
    position: relative;
    padding-bottom: 30px;
}

.company__lead {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.company__text {
    padding: 0;
    margin: 0;
    font-size: 15px;
}

.company__person-add {
    position: relative;
    padding: 15px 15px 23px 49px;
    margin-left: 10px;
    margin-bottom: 10px;
    font-size: 12px;
    width: 220px;
    line-height: 1.4;
    font-family: "TTFirsNeue-medium";
    background-repeat: no-repeat;
    background-image: url("../img/icon-blockquote.svg");

    &::before {
        content: "";
        position: absolute;
        top: 25px;
        left: 15px;
        width: 24px;
        height: 24px;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url("../img/icon-notif.svg");
    }
}

.company__person--main {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
}

.company__person--main .company__about-person {
    padding-top: 5px;
    width: 100%;
}

.company__director {
    margin-bottom: 20px;
}

.company__person {
    margin-bottom: 30px;
}

.company__about-person {
    padding-top: 5px;
}

.company__img {
    margin-bottom: 10px;
}

.company__person-name {
    display: block;
    margin-bottom: 5px;
    font-size: 18px;
    color: #000000;
    font-style: normal;
    font-family: "TTFirsNeue-demibold";
}

.company__post {
    padding: 0;
    margin: 0;
    margin-bottom: 13px;
    font-size: 14px;
    color: #C79B67;
}

.company__person-text {
    font-size: 15px;
    padding-right: 3px;
}

.company__video {
    margin-bottom: 20px;
}

.company .video__wrap {
    position: relative;
    z-index: 100;
    width: 100%;
    //height: auto;
    //padding-top: 80%;   
    height: 215px; 
    margin-bottom: 29px;
    background-color: transparent;
    border-radius: 18px;
    transition: .1s ease-in;

    video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 18px;
    }
}

.company .video-controls {
    position: absolute;
    top: 25%;
    left: 5%;
    z-index: 2;
}

.company .play-pause {
    position: relative;
    top: 0;
    left: 0;
    width: 44px;
    height: 44px;
    background-color: rgba(187, 28, 38, 0.6);
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/icon-play.svg");
    border: 0;
    border-radius: 50%;
    outline-offset: 5px;
    outline: 1px solid rgba(255,57,53,.2);
    font-size: 0;
    z-index: 1000;
    transition: .5s ease-in;
    cursor: pointer;

    &:hover {
        background-color: rgba(187, 28, 38, 1);
        transition: .5s ease-in;
    }

    &:active {
        background-color: rgba(187, 28, 38, 1);
        transition: .5s ease-in;
    }
}

.company .play-pause--hidden {
    opacity: 0;
}

.company .video__text {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 20px;
    z-index: 1;    
    color: #ffffff;
    font-size: 14px;
    letter-spacing: 0.02em;
    font-family: "TTFirsNeue-demibold";
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
    border-radius: 18px;
}

.company .video-opened {
    .video__text {
        display: none;
    }
}

.company__list {
    padding: 0;
    margin: 0;
    margin-bottom: 38px;
    list-style: none;
}

.company__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px;
    margin-bottom: 18px;
    border: 1px solid #efefef;
    border-radius: 16px;

    span {
        font-size: 14px;
    }
}

span.company__count {   
    font-family: "Viga";
    color: #B4864F;
    font-size: 30px;
    width: 64px;
    margin-right: 10px;
    text-align: center;
    flex-shrink: 0;
}

.company__partners-list {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.company__partners-item {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #efefef;
    border-radius: 16px;
    padding: 20px 0;
    height: 112px;

    a {
        display: block;
        cursor: pointer;
        position: relative;
        width: 129px;
        height: 74px;
    }
}   

.company__title--partners {
    margin-bottom: 40px;    
}

.company__partners-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
    margin-bottom: 0;
}

.company__partners-img--fifth {
    object-fit: cover;
}

@media (min-width: 380px) {
    .company .video__wrap {
        height: 250px;
    }    

    .company .play-pause {
        width: 64px;
        height: 64px;
    }   
}   

@media (min-width: 400px) {
    .company .video__wrap {
        height: 270px;
    }    

    .company .video-controls {
        top: 32%;
    }
}  


@media (min-width: 576px) {
    .company {
        padding-top: 50px;
        padding-bottom: 25px;
    }

    .company__title {
        font-size: 36px;
        margin-bottom: 30px;
    }

    .company__lead {
        font-size: 20px;
        margin-bottom: 15px;
    }

    .company__text {
        font-size: 16px;
        margin-bottom: 40px;
    }

    .company__img {
        margin-top: 5px;
    }

    .company__team {
        padding-top: 30px;
    }

    .company__director {
        position: relative;
        padding-top: 45px;
    }

    .company__person-add {
        position: absolute;
        display: flex;
        align-items: center;
        top: -60px;
        left: 0;
        padding: 1px 22px 20px 74px;
        margin-left: 0;
        width: 100%;
        background-image: url("../img/icon-blockquote-2.svg");
        background-size: contain;
        background-repeat: no-repeat;
        width: 100%;
        z-index: 10;        

        &::before {
            top: 15px;
            left: 21px;
            width: 34px;
            height: 34px;
            background-image: url("../img/icon-notif-big.svg");
        }

        span {
            width: 100%;
            background-color: #ffffff;
            padding: 12px 0 12px;
            font-size: 14px;
            line-height: 1.4;
            max-width: 410px;
        }
    }    

    .company__person {
        display: grid;
        grid-template-columns: 100px 1fr;
        column-gap: 30px;
        margin-bottom: 37px;
        align-items: start;
    }    

    .company__video {
        margin-bottom: 58px;
    }

    .company .video-controls {
        top: 38%;
        left: 12%;
    }

    .company .video__wrap {
        height: 370px;
    }  

    .company .video__text {
        padding: 40px;
        font-size: 18px;
    }    

    .company__item {
        padding: 20px 40px;
        margin-bottom: 26px;
    }

    span.company__count {
        width: 95px;
        font-size: 44px;
        margin-right: 20px;
    }    

    .company__title--partners {
        text-align: left;
    }

    .company__partners-list {
        margin-bottom: 0;
        gap: 30px;
    }

    .company__partners-item a {
        width: 200px;
    }   
}    

@media (min-width: 600px) {
    .company .video__wrap {
        height: 410px;
    }  

    .company .video-controls {
        top: 44%;
    }
}    


@media (min-width: $tablet-width) {
    .company {
        padding-top: 18px;
        padding-bottom: 35px;
    }

    .company__title {
        font-size: 36px;
    }

    .company__director {
        padding-top: 53px;
    }

    .company__person-add {
        top: -50px;
        width: 100%;

        span {
            padding: 25px 0;
            max-width: 515px;
            margin-top: -1px;
            border-top: 1px solid #efefef;
            border-bottom: 1px solid #efefef;
            border-right: 1px solid #efefef;
            border-radius: 20px;
        }
    }    

    .company__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
    }

    .company__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 20px 32px;

        span {
            text-align: center;
        }
    }

    span.company__count {
        margin-right: 0;
        margin-bottom: 7px;
    }

    .company__partners-list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 30px;
    }

    .company__video {
        margin-bottom: 45px;
    }

    .company .video__wrap {
        height: 460px;
        margin-bottom: 40px;
    }  

    .company .video-controls {
        top: 51%;
        left: 9%;
    }

    .company__partners-item {
        height: 114px;
    }

    .company__partners-item a {
        width: 170px;
        height: 74px;
    }   
}    

@media (min-width: 992px) {
    .company {
        padding-top: 70px;
    }

    .company__title {
        font-size: 40px;
    }

    .company__text-wrap {
        padding-bottom: 13px;
    }

    .company__team {
        padding-top: 0;
    }

    .company__director {
        padding-top: 0;        

        .company__about-person {
            margin-bottom: 15px;
            padding-left: 10px;
            padding-right: 20px;
        }
    }

    .company__person.company__person--main {
        display: grid;
        grid-template-columns: 250px 1fr;        
        margin-bottom: 60px;
     }

    .company__person {
       display: block;
       margin-bottom: 12px;
    }

    .company__person-text {
        font-size: 16px;
    }

    .company__person-add {
       top: 4px;
       left: auto;
       right: 0;
       width: calc(100% - 290px);
       background-image: url("../img/icon-blockquote-3.svg");

       span {
        padding-top: 24px;
        padding-bottom: 21px;
        border-top: none;
        border-bottom: none;
        border-right: none;
        margin-top: 0;
       }
    }   

    .company__img {
        width: 140px;
        height: 140px;
        margin-bottom: 12px;
    }
    
    .company__person--main .company__img {
        width: 250px;
        height: 250px;
        margin-bottom: 0;
    }

    .company__person--main {
        grid-template-columns: 250px 1fr;
        align-items: end;
    }

    .company__persons {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .company__post {
        margin-bottom: 18px;
    }

    .company__video {
        margin-bottom: 62px;
    }

    .company .video__wrap {
        height: 540px;
    }  

    .company .video__text {
        padding: 50px 60px;
        font-size: 20px;
    }

    .company .video-controls {
        top: 60%;
        left: 6%;
    }

    .company__item {
        padding: 30px;
    }   

    .company__item span {
        text-align: left;
        font-size: 16px;
    }

    span.company__count {
        width: 100%;
        margin-bottom: 14px;
        font-size: 40px;
    }   

    .company__partners-item {
        height: 174px;
    }

    .company__partners-item a {
        width: 210px;
        height: 94px;
    }   

}    

@media (min-width: $desktop-width) {
    .company {
        padding-top: 10px;
        padding-bottom: 110px;
    }

    .company__title {
        margin-bottom: 52px;
    }

    .company__add {        
        width: 280px;
        bottom: 50%;        
        transform: translateY(50%);
        right: 0;
    }   

    .company__lead {
        padding-right: 320px;
    }

    .company__text {
        max-width: 780px;
    }

    .company__team {
        display: grid;
        grid-template-columns: 1fr 380px;
        column-gap: 15px;        
    }

    .company__persons {
        gap: 0;
    }


    .company__director { 
        order: 1;
    }   

    .company__director .company__about-person {
        padding-left: 0;
        padding-right: 0;
    }   

    .company__person.company__person--main {
        display: block;
        padding-right: 0;
        margin-bottom: 12px;
    }

    .company__person-add {       
        top: -140px;
        width: 380px;
        background-image: url("../img/icon-blockquote-4.svg");

        &::before {
            top: 25px;
        }

        span {
            padding-top: 22px;
        }

    }

    .company__person--main .company__img {
        width: 380px;
        height: 527px;
        margin-bottom: 28px;
    }

    .company__img {        
        margin-bottom: 17px;
    }

    .company__person {
        display: block;
        padding-right: 63px;
        margin-bottom: 56px;
    }    

    .company__post {
        margin-bottom: 20px;
    }

    .company__person-text {
        padding-right: 4px;
    }

    .company__video {
        display: grid;
        grid-template-columns: 880px 1fr;
        column-gap: 40px;
    }

    .company .video__wrap {
        margin-bottom: 0;
    }

    .company__list {
        display: flex;
       flex-direction: column;
       justify-content: space-between;
        flex-wrap: wrap;
    }

    .company__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        padding: 45px 40px;
        margin-bottom: 34px;
    }

    span.company__count {
        width: 100px;
        margin-bottom: 0;
        font-size: 44px;
        margin-right: 13px;
        text-align: center;
    }    

    .company__partners-list {
        gap: 40px;
    }

    .company__partners-item {
        height: 224px;
        margin-bottom: 0;
    }

    .company__partners-item a {
        width: 340px;
        height: 144px;
    }

    .company__partners-item--second a {
        width: 95px;
        height: 90px;

        .company__partners-img {
            object-fit: cover;
        }
    }

    .company__partners-img--fifth {
        object-fit: contain;
    }

}    