.product {
    padding-bottom: 15px;
}

.product__slide {
    position: relative;
    width: 100%;
    height: 260px;
    margin-bottom: 15px;
}

.product__image {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
}

.product__overlay {
    display: none;
    max-width: 100%;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(84, 59, 30, 0) 0%, rgba(84, 59, 30, 0.8) 100%);
    border-radius: 18px;
    z-index: 2;
}

.product__slide .product__image {
    border-radius: 20px;
}

.product__slide-mini .product__image {
    border-radius: 18px;
}

.product__slide-mini {   
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 35px;    
}

.product__slide-mini.active {
    .product__wrap-mini .product__overlay {
        display: block;
    }
}

.product__wrap-mini {
    position: relative;
    width: 120px;
    height: 80px;    
    cursor: pointer;
}

.product__description {
    margin-bottom: 40px;    
}

.product__title {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    color: #000000;
    font-size: 24px;
    font-family: "TTFirsNeue-Bold";
    letter-spacing: 0.02em;
}

.product__features {
    margin-bottom: 28px;
}

.product__feature {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-bottom: 8px;
}

.product__feature::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: #eee;
}

.product__feature span {
    background-color: #fff;
    z-index: 10;
    padding-right: 10px;
    font-size: 15px;
}

.product__feature span:nth-child(2n) {
    padding-left: 10px;
    padding-right: 0;
    font-family: "TTFirsNeue-medium";
    color: #000000;
}

.product__text span {
    display: block;
    font-size: 16px;
    color: #000000;
    font-family: "TTFirsNeue-medium";
}

.product__text {
    color: #C79B67;
    font-size: 30px;
    font-family: "TTFirsNeue-Bold";
    margin-bottom: 28px;
}

.product__send {
    width: 100%;
}

.product__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.product__item {
    position: relative;
    padding: 21px 20px 21px 88px;
    border: 1px solid #efefef;
    border-radius: 16px;
    color: #000000;
    font-family: "TTFirsNeue-medium";
    margin-bottom: 17px;

    &::before {
        content: "";
        position: absolute;
        top: 20px;
        left: 20px;
        width: 45px;
        height: 45px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.product__item--date {

    &::before {
        background-image: url("../img/icon-date.svg");
    }
   
}

.product__item--mounting {

    &::before {
        background-image: url("../img/icon-mounting.svg");
    }
   
}

@media (min-width: 576px) {
    .product {
        padding-top: 5px;
        padding-bottom: 10px;
    }

    .product__slider-mini {
        margin: 0 -5px;
    }

    .product__slide {
        height: 380px;
        margin-bottom: 10px;
    }

    .product__wrap-mini {
        width: 140px;
        height: 100px;
    }

    .product__slide .product__image {
        border-radius: 24px;
    }

    .product__title {
        font-size: 30px;
        margin-bottom: 29px;
    }

    .product__description {
        display: grid;
        grid-template-columns: 1fr 240px;
        align-items: center;
        margin-bottom: 45px;
    }

    .product__features {
        grid-column: 1/3;
        margin-bottom: 35px;
    }

    .product__text {
        margin-bottom: 0;
    }

    .product__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .product__item {
        padding: 98px 30px 30px 30px;

        &::before {
            top: 30px;
            left: 30px;
        }
    }
}    

@media (min-width: $tablet-width) {
    .product__slide .product__image {
        border-radius: 30px;
    }

    .product__slide-mini {
        margin-bottom: 38px;
    }

    .product__description {
        grid-template-columns: 1fr 330px;
    }

    .product__item {
        padding: 30px 30px 30px 108px;

        &::before {
            top: 30px;
            left: 30px;
        }
    }

    .product__text {
        font-size: 32px;
    }
}    

@media (min-width: 992px) {

    .product {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        align-items: flex-start;
    }

    .product__sliders {
        display: block;
        width: calc(100% - 400px);
    }

    
    .product__slider-mini {
        margin: 0 0 0 -5px;
    }    

    .product__slide {
        height: 340px;
    }

    .product__title {
        margin-bottom: 17px;
    }

    .product__description {
        width: 370px;
        display: block;
        margin-bottom: 40px;
    }

    .product__text {
        margin-bottom: 25px;
    }

    .product__list {
        width: 100%;
    }

    .product__item {
        padding: 42px 40px 42px 108px;
    }

}    

@media (min-width: $desktop-width) {
    .product {
        position: relative;
    }

    .product__slide {
        height: 460px;
    }

    .product__sliders {
        width: 650px;
    }

    .product__slide {
        margin-bottom: 20px;
    }

    .product__slide-mini {
        padding-left: 7px;
        padding-right: 7px;
        margin-bottom: 0;
    }

    .product__title {
        font-size: 36px;
        margin-bottom: 32px;
    }

    .product__feature {
        margin-bottom: 13px;
    }

    .product__description {
        display: grid;
        grid-template-columns: 1fr 251px;
        column-gap: 60px;
        width: calc(100% - 650px - 60px);
        //width: 630px;
        
    }

    .product__text {
        margin-bottom: 30px;
    }

    .product__send {
        margin-bottom: 30px;
    }

    .product__list {
        position: absolute;
        right: 25px;
        bottom: 9px;
        width: calc(100% - 650px - 60px - 50px);
        //width: 630px;
    }

    .product__item {   
        padding: 31px 30px 31px 108px;     
        margin-bottom: 0;
    }

}    



