.breadcrump {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

.breadcrump__list {
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-start;

    li {
        position: relative;
        font-size: 13px;
        margin-right: 32px;

        &:last-child {
            margin-right: 0;

            &::before {
                content: "";
                width: 0;
                height: 0;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 5px;
            right: -24px;
            width: 10px;
            height: 10px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("../img/icon-page.svg");
        }
    }
}

.breadcrump-link {
    color: #585858;
    font-size: 13px;

    &:hover {
        color: #BB1C26;
    }

    &:active {
        color: #BB1C26;
    }
}

.breadcrump__list li:last-child {
    color: #000000;
}

@media (min-width: 576px) {
    .breadcrump {
        padding-top: 33px;
        padding-bottom: 27px;
    }
}   

@media (min-width: $tablet-width) {
    .breadcrump {
        display: block;
    }
}    

@media (min-width: 992px) {
    .breadcrump {
        padding-top: 46px;
        padding-bottom: 35px;
    }
}    

@media (min-width: $desktop-width) {
    .breadcrump {
        padding-bottom: 63px;
    }
}    

