.not-found {
    margin-bottom: 40px;
}

.not-found__title {
    padding: 0;
    margin: 0;
    margin-bottom: 30px;
    font-family: "TTFirsNeue-Bold";
    font-size: 30px;
    color: #000000;
    text-align: center;
    letter-spacing: 0.02em;
}

.not-found__img {
    display: block;
    margin: 0 auto 40px;
}

.not-found__note {
    position: relative;
    padding: 20px 20px 5px 20px;
    margin-bottom: 30px;
    border-radius: 30px;
    border: 10px solid #EFEFEF;

    &::before {
        content: "";
        position: absolute;
        top: 28px;
        left: 20px;
        height: 20px;
        width: 32px;
        height: 32px;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../img/icon-attention.svg");
    }
}

.not-found__note-title {
    padding: 0;
    padding-left: 49px;
    margin: 0;
    margin-bottom: 20px;
    font-size: 18px;
    color: #000000;
    font-family: "TTFirsNeue-demibold";
}

.not-found__list {
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    list-style: none;
}

.not-found__item {
    position: relative;
    font-size: 14px;
    padding-left: 33px;
    margin-bottom: 11px;

    &::before {
        content: "";
        position: absolute;
        top: 10px;
        left: 15px;
        height: 4px;
        width: 4px;
        background-color: #C79B67;
    }
}

.not-found__link {
    display: block;
    margin-bottom: 15px;
}


@media (min-width: 450px) {
    .not-found__img {
        width: 75%;
        height: auto;
    }
}    

@media (min-width: 576px) {
    .not-found {
        margin-bottom: 64px;
    }

    .not-found__title {
        font-size: 36px;
    }

    .not-found__links {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }

    .not-found__link {
        margin-bottom: 0;
    }

    .not-found__img {
        margin-bottom: 45px;
    }    

    .not-found__note {
        padding: 30px;
    }    

    .not-found__list {
        margin-bottom: 35px;
    }   
    
    .not-found .button {
        padding-top: 20px;
        padding-bottom: 20px;
    }
}    

@media (min-width: $tablet-width) {
    .not-found {
        margin-bottom: 70px;
    }

    .not-found__title {
        text-align: left;
    }

    .not-found__note-title {
        font-size: 24px;
        margin-bottom: 24px;
    }    

    .not-found__item {
        font-size: 15px;
        padding-left: 37px;
        margin-bottom: 17px;
    }

    .not-found__img {
        width: 67%;
        margin-bottom: 50px;
    }   

    .not-found__note {
        padding-top: 25px;
        border: 15px solid #efefef;
    }

    .not-found__note::before {
        top: 32px;
        left: 33px;
    }    

    .not-found .button {
        padding-top: 24px;
        padding-bottom: 24px;
    }
}    

@media (min-width: 992px) {
    .not-found {
        margin-bottom: 94px;
    }

    .not-found__title {
        font-size: 40px;
        margin-bottom: 55px;
    }

    .not-found__img {
        width: 450px;
        margin-bottom: 62px;
    }   

    .not-found__list {
        margin-bottom: 41px;
    }

    .not-found__note {
        padding: 50px;

        &::before {
            top: 50px;
            left: 50px;
        }
    }
}    

@media (min-width: $desktop-width) {
    .not-found {
        margin-bottom: 0;
    }

    .not-found__title {
        margin-bottom: 46px;
    }

    .not-found__wrap {
        display: grid;
        grid-template-columns: 1fr 535px;
        column-gap: 80px;
        align-items: flex-start;
    }

    .not-found__img {
        order: 1;
        width: 535px;
        padding-bottom: 20px;
    }

    .not-found__note {
        margin-top: 30px;
    }

    .not-found__list {
        margin-bottom: 45px;
    }
}    